<template>
  <router-link class="article-item" :to="linkTo">
    <img :src="imageShorcut" alt class="article-item__shorcut">
    <div class="article-item__information">
      <h3 class="information__title">{{ title }}</h3>
      <p class="information__description">{{ shortDescription }}</p>
    </div>
  </router-link>
</template>

<script>
export default {
  name: "article-item",
  props: {
    title: String,
    shortDescription: String,
    isPublished: Boolean,
    publishedDate: String,
    linkTo: String,
    imageShorcut: String
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.article-item {
  display: inline-block;
  width: 100%;
  height: 100%;
  padding-top: 100%;
  position: relative;
  overflow: hidden;
  &:hover {
    .article-item__information {
      opacity: 1;
    }
  }
  .article-item__shorcut {
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%);
    bottom: 50%;
    left: 0;
    right: 0;
  }
  .article-item__information {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(255, 255, 255, 0.8);
    padding: 30px;
    color: #000000;
    opacity: 0;
    transition: opacity 0.4s ease;
    .information__title {
      font-size: 25px;
    }
  }
}
</style>
